
.services {
    text-align: center;
    background-color: #f9f9f9;
    width: 1499px;
    margin-top: -30px;
  
  }
  
  .services h2 {
    font-size: 32px;
    margin-bottom: 40px;
    /* width: 1000px; */
    margin-top: 1px;
  }
  
  .services-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    justify-content: center;
    gap: 4.2rem;
    /* flex-wrap: wrap; */
    overflow: hidden;
    /* margin-left: -16rem; */
  }
  
  .service-item {
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    width: 300px;
    text-align: start;
    transition: transform 0.3s, box-shadow 0.3s;
  }
  
  .service-item:hover {
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
  }
  
  .service-item img {
    width: 42px;
    height: 42px;
    margin-bottom: 20px;
  }
  
  .service-item h3 {
    font-size: 20px;
    color: #333;
    margin-bottom: 10px;
  }
  
  .service-item p {
    font-size: 16px;
    color: #666;
  }
  
